html {
  min-height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background-color: hsl(0, 100%, 74%);
  padding-bottom: 0;
}
.container{
  width: 100%;
  background-image: url('images/bg-intro-desktop.png');
  background-attachment: fixed;
  background-position:center;
  background-size: cover;
}
.container-form{
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin: 0 auto;
  min-height: 300px;
}
.form{
width: 50%;
padding: 20px;
}
.cont-cita{
width: 50%;
padding: 20px;
}
h3{
  text-align: center;
  padding: 16px;
  margin: 10px;
  box-sizing: border-box;
  border-radius: 8px;
  background-color:hsl(248, 32%, 49%);
  font-weight: 500;
  color: #fff;
  max-width: 440px;
  float: right;
  width: 100%;
  box-shadow: 0px 5px 1px rgb(0 0 0 / 20%);
}

h2{
  color: #FFFFFF;
  text-align: center;
  font-size: 33px;
  font-weight: bolder;
}

form{
  background-color: #FFFFFF;
  padding: 20px;
  margin: 10px;
  border-radius: 12px;
  box-sizing: border-box;
  box-shadow: 0px 6px 1px rgb(0 0 0 / 20%);
  max-width: 440px;
  float: right;
}
input,textarea{
  width: 100%;
  outline: none;
  padding: 15px 20px;
  border: 1px solid;
  color:hsl(246, 25%, 77%);
  border-radius: 5px;
  font-weight: 600;
  position: relative;
  margin: 10px 0 10px 0;
  box-sizing: border-box;
  resize: none;
}

button{
  width: 100%;
  padding: 13px 30px;
  border-radius: 5px;
  background-color: hsl(154, 59%, 51%);
  border: 4px solid transparent;
  cursor: pointer;
  border-bottom: 4px solid hsl(154deg 54% 42%);
  text-transform: uppercase;
  color: #FFFFFF;
  font-size: 17px;
  font-weight: 500;
}
button:hover{
  background-color: hsl(154, 58%, 46%);
}

.reunion {
  background-color: #FFFFFF;
  padding: 20px;
  margin: 10px;
  border-radius: 12px;
  box-sizing: border-box;
  box-shadow: 0px 6px 1px rgb(0 0 0 / 20%);
  max-width: 440px;
  margin: 0 auto;
  margin-bottom: 20px;
  margin-top: 5px;
  /* color :hsl(246, 25%, 77%); */
}
.reunion h4{
  margin: 5px;
  padding: 0;
  font-size: 20px;
}
.reunion h6{
  margin: 5px;
  padding: 0;
  color:hsl(246, 25%, 77%);
}

.reunion p {
  font-size: 14px;
  margin-bottom: .8rem;
}
.reunion small{
  color: hsl(245deg 23% 80%);
}
.reunion button{
  margin-top: 10px;
  font-size: 13px;
}


.p-error {
  color: red;
  padding: 2px;
  font-size: 11px;
  text-transform: uppercase;
  text-align: center;
} 


@media only screen and (max-width:890px){

  .container-form{
    width: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  h3{
    float: none;
  }
  
  .form{
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding:0;
  }
  .form form{
    float: none;
  }
  .cont-cita{
    width: 80%;
    padding: 0;
  }
  h2{
    font-size: 20px;
  }
}